import React from 'react'
import { Link } from 'gatsby'
import styled from '@emotion/styled'
import Headroom from 'react-headroom'
import logo from '../../static/logo/dumna.svg'

const StyledLink = styled(Link)`
    position: fixed;
    display: inline-block;
    top: -15px;
    width: 355px;
    transition: all 0.25s ease-out
`

const Nav = styled.nav`
    position: relative;
    transform: translateY(45%);
    display: flex;
    justify-content: center;
    font-weight: 500;
    font-size: 1.25rem;
    align-items: center;
    height: 50px;
    top: 268px;
    box-shadow: 0px 2px 12px 0px;
    align-items: center;
    background: #A92228;
    width: 100%;
    a {
        color: #ded3d3;
        padding-left: 2rem;
        opacity: 0.95;
        transition: all ${props => props.theme.transitions};
        overflow: hidden;
        &:hover>div {
            width: 100%;
            left: 0%;
            opacity: 0.9;
            box-shadow: 0px 0px 0px 0px;
            background-color: #f0f0f0;
            transition: all 0.35s ease-out;
        }
        div {
            position: relative;
            height: 2px;
            width: 0%;
            top: 1px;
            left: 50%;
            opacity: 0.5;
            background-color: #ded3d3;
            transition: all 0.35s ease-out;
        }
    }
`

const Pinned = function() {
    document.getElementById("content-header-logo").style.opacity = "0";
    document.getElementById("nav").style.transform = "translateY(45%)";
};

const unPinned = function() {
    document.getElementById("content-header-logo").style.opacity = "1";
    document.getElementById("nav").style.transform = "translateY(0%)";
};
const unFixed = function() {
    document.getElementById("content-header-logo").style.opacity = "1";
    document.getElementById("nav").style.transform = "translateY(45%)";
};

const ContentNavBar = () => (
    <Headroom className="content_headroom" disableInlineStyles pin hide content instantSnap
    onPin={Pinned} onUnpin={unPinned} onUnfix={unFixed} 
    fixAnchor={270} pinAnchor={0} hideAnchor={1000} >
        <StyledLink id="content-header-logo" to="/">
            <img src={logo} alt='Dumna Niepodległa Logo' />
        </StyledLink>
        <Nav id="nav">
            <Link to='/'>
                Wydarzenia
                <div />
            </Link>
            <Link to='/'>
                O nas
                <div />
            </Link>
            <Link to='/'>
                Kontakt
                <div />
            </Link>
            <Link to='/'>
                Sklep
                <div />
            </Link>
        </Nav>
    </Headroom>
);

export default ContentNavBar